import { defineStore } from 'pinia';
import 'firebase/auth';


export const useUserStore = defineStore({
  id: 'user',

  state: () => ({
    userInfo: {
      name: '',
      email: '',
      avatar: '',
      auth: '',
      token: ''
    }
  }),

  actions: {
    setUserInfo(userInfo) {
      this.userInfo = userInfo;
    },
  }
});
