import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'


const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/login',
    name: 'login',
    component: () => import(/* webpackChunkName: "login" */ '../views/FirebaseLogin.vue')
  },
  {
    path: '/author',
    name: 'author',
    component: () => import(/* webpackChunkName: "author" */ '../views/AuthorView.vue')
  },
  {
    path: '/reader',
    name: 'reader',
    component: () => import(/* webpackChunkName: "reader" */ '../views/ReaderView.vue')
    //props: route => ({ story: route.params.story })
  },
  {
    path: '/community',
    name: 'community',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // 异步加载路由../views/CommunityView.vue../views/CommunityView.vue
    component: () => import(/* webpackChunkName: "community" */ '../views/CommunityView.vue')
  },
  {
    path: '/tutorial',
    name: 'tutorial',
    component: () => import(/* webpackChunkName: "tutorial" */ '../views/TutorialView.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
